export interface CreateSupportTicketData {
  title: string;
  description: string;
  category: string;
  priority: string;
  attachments?: any[];
  tags?: string[];
}

export interface UpdateSupportTicketData {
  title?: string;
  description?: string;
  category?: string;
  priority?: string;
  status?: string;
  attachments?: any[];
  tags?: string[];
}

export interface AddTicketMessageData {
  message: string;
  attachments?: any[];
}

export interface SupportTicketFilters {
  search?: string;
  status?: string;
  category?: string;
  priority?: string;
  page?: number;
  limit?: number;
}

// API Response types
interface ApiResponse<T> {
  success: boolean;
  message: string;
  data: T;
  count?: number;
  total?: number;
}

// Get all support tickets
export const getAllSupportTickets = async (
  filters: SupportTicketFilters = {}
): Promise<ApiResponse<SupportTicket[]> & { total: number }> => {
  try {
    const params = new URLSearchParams();
    
    if (filters.search) params.append("search", filters.search);
    if (filters.status) params.append("status", filters.status);
    if (filters.category) params.append("category", filters.category);
    if (filters.priority) params.append("priority", filters.priority);
    if (filters.page) params.append("page", filters.page.toString());
    if (filters.limit) params.append("limit", filters.limit.toString());

    const response = await apiClient.get(`/api/support/tickets?${params.toString()}`);
    
    return {
      ...response.data,
      total: response.data.count || (response.data.data ? response.data.data.length : 0),
    };
  } catch (error: any) {
    console.error("❌ Error fetching support tickets:", error);
    console.error("❌ Error response:", error.response);
    console.error("❌ Error status:", error.response?.status);
    console.error("❌ Error data:", error.response?.data);
    console.error("❌ Request config:", error.config);
    throw new Error(error.response?.data?.message || error.message || "Failed to fetch support tickets");
  }
};

// Get support ticket by ID
export const getSupportTicketById = async (ticketId: string): Promise<ApiResponse<SupportTicket>> => {
  try {
    const response = await apiClient.get(`/api/support/tickets/${ticketId}`);
    return response.data;
  } catch (error: any) {
    console.error("Error fetching support ticket:", error);
    throw new Error(error.response?.data?.message || "Failed to fetch support ticket");
  }
};

